// Webrtc plugin.
import { trackActions } from '../interface/actions'

// Other plugins.
import { logManager } from '../../logs'

// Libraries.
import { call, put, all } from 'redux-saga/effects'
import { isUndefined } from 'lodash/fp'

const log = logManager.getLogger('MEDIA')

/**
 * Render Tracks in a specified container.
 * @method renderTracks
 * @param  {Object} action A "render tracks" action.
 */
export function * renderTracks (webRTC, action) {
  const { trackIds, selector, speakerId } = action.payload
  log.info(`Rendering track(s) in element ${selector}.`, trackIds)

  // Get the tracks that are to be rendered.
  const tracks = yield call([webRTC.track, 'getTracks'], trackIds)
  const filteredTracks = tracks.filter(track => !isUndefined(track))

  // Render the tracks.
  yield all(filteredTracks.map(track => call([track, 'renderIn'], selector, speakerId)))

  log.info(`Finished rendering track(s).`, filteredTracks.map(track => track.id))
  // Report operation done.
  yield put(
    trackActions.renderTracksFinish(filteredTracks.map(track => track.id), {
      selector
    })
  )
}

/**
 * Stop Tracks from being rendered in a specified container.
 * @method removeTracks
 * @param  {Object} action A "remove tracks" action.
 */
export function * removeTracks (webRTC, action) {
  const { trackIds, selector } = action.payload
  log.info(`Removing track(s) from element ${selector}.`, trackIds)

  // Get the tracks that are to be removed.
  const allTracks = yield call([webRTC.track, 'getTracks'])
  const tracks = allTracks.filter(track => trackIds.includes(track.id))

  // Remove the tracks.
  yield all(tracks.map(track => call([track, 'removeFrom'], selector)))

  log.info(`Finished removing track(s).`, tracks.map(track => track.id))
  // Report operation done.
  yield put(
    trackActions.removeTracksFinish(tracks.map(track => track.id), {
      selector
    })
  )
}

/**
 * Mute certain tracks.
 * @method muteTracks
 * @param  {Object} action
 * @param  {Array} action.payload A list of track IDs.
 */
export function * muteTracks (webRTC, action) {
  log.info(`Muting track(s).`, action.payload)
  // Get the tracks that are to be muted.
  const allTracks = yield call([webRTC.track, 'getTracks'])
  const tracks = allTracks.filter(track => action.payload.includes(track.id))

  // Mute the tracks.
  yield all(tracks.map(track => call([track, 'mute'])))

  log.info(`Finished muting track(s).`, tracks.map(track => track.id))
  // Report operation done.
  yield put(trackActions.muteTracksFinish(tracks.map(track => track.id)))
}

/**
 * Unmute certain tracks.
 * @method unmuteTracks
 * @param  {Object} action
 * @param  {Array} action.payload A list of track IDs.
 */
export function * unmuteTracks (webRTC, action) {
  log.info(`Unmuting track(s).`, action.payload)

  // Get the tracks that are to be unmuted.
  const allTracks = yield call([webRTC.track, 'getTracks'])
  const tracks = allTracks.filter(track => action.payload.includes(track.id))

  // Unmute the tracks.
  yield all(tracks.map(track => call([track, 'unmute'])))

  log.info(`Finished unmuting track(s).`, tracks.map(track => track.id))
  // Report operation done.
  yield put(trackActions.unmuteTracksFinish(tracks.map(track => track.id)))
}
