import * as actionTypes from './interface/actionTypes'
import * as actions from './interface/actions'

import { call, put, takeEvery } from 'redux-saga/effects'
import { toQueryString, mergeValues } from '../common/utils'
import { logManager } from '../logs'

// State setters.
import { update as updateConfig } from '../config/interface/actions'

/**
 * Enum declaring the valid request response data types that are available to be handled
 */
const responseTypes = Object.freeze({
  json: 'json',
  blob: 'blob',
  text: 'text',
  none: 'none'
})

const contentTypes = Object.freeze({
  jsonType: 'application/json',
  vdnJsonType: 'application/vdn.kandy.json',
  plainTextType: 'text/plain',
  xmlTextType: 'text/xml',
  octetStream: 'application/octet-stream'
})

const pluginName = 'requests'

/**
 * Configurable properties 'published' by this "Request" plugin.
 *
 * @property {boolean} injectAgentVersionHeader Option to automatically inject an agent version header to every REST request.
 *            This header is used to help with diagnostics and analytics in a completely anonymous fashion.
 *            TODO: Set it to 'true' after server side whitelists that actual custom header.
 */
const defaultOptions = {
  injectAgentVersionHeader: true
}

/*
 * HTTP request plugin.
 */
export default function request (options = {}) {
  options = mergeValues(defaultOptions, options)

  function * init () {
    yield put(updateConfig(options, pluginName))
  }

  return {
    sagas: [watchRequests],
    name: pluginName,
    init
  }
}

/*
 * Saga to watch for every request action.
 */
function * watchRequests () {
  yield takeEvery(actionTypes.REQUEST, handleRequest)
}

/*
 * Generator that handles a request action with standard HTTP handling features and reports
 * a requestFinished action when the request is done.
 *
 * @param {FluxStandardAction} action The action to handle.
 */
function * handleRequest (action) {
  // Make the request based on the action
  var result = yield call(makeRequest, action.payload, action.meta.requestId)
  yield put(actions.response(action.meta.requestId, result, !!result.error))
}

/**
 * Make a request with the specified options. The options is very similar to the options passed to the GlobalFetch
 * method except that is also accepts the url as part of the options.
 *
 * Currently this processes the request and assumes nothing about the response.
 * - If the response has a body, it will always be parsed and forwarded on.
 *      - If no body, then an empty object in its place.
 * - If the fetch succeded, the response "results" will always be forwarded on,
 *      even if the response is outside of the 200-299 range.
 *      - Because some backends provide more detailed error info (that a saga
 *        may need) as part of the body, rather than just the response status.
 *
 * @param {Object} options Options to make the request with.
 * @param {string} options.url The URL to make the request to.
 * @param {Object} [options.queryParams] The parameters to be added to the query string
 * @param {string} [options.responseType] The data type assumed to be received in the response body
 * @param {Blob|BufferSource|FormData|UrlSearchParams|string} [options.body] The request body
 * @return {Promise} A promise that resolves with a custom response object.
 */
async function makeRequest (options, requestId) {
  const log = logManager.getLogger('REQUEST', requestId)

  // Extract and remove the non-fetch API properties.
  const { url, queryParams, responseType = 'json', ...fetchOptions } = options

  if (!url || typeof url !== 'string') {
    const invalidUrlMessage = `Invalid request url; expected url of type string but received ${url} instead`
    log.error(invalidUrlMessage)
    return {
      body: undefined,
      error: 'REQUEST_URL',
      message: invalidUrlMessage
    }
  }

  // Grab that last part of the URL (after the last /) to be logged.
  let endUrl = url.match(/([^/]*)$/)[0]
  // Cut it short if it's too long, since this should be human-readable.
  endUrl = endUrl.length > 15 ? endUrl.substring(0, 15) + '...' : endUrl
  log.info(`Making ${fetchOptions.method} ${endUrl} request.`)

  if (!responseTypes.hasOwnProperty(responseType)) {
    // Invalid data type requested
    log.info('Cannot make request; responseType value was invalid.')
    return {
      body: undefined,
      error: 'RESPONSE_TYPE',
      message: 'Requested invalid data type for response'
    }
  }
  let response
  let contentType
  try {
    response = await fetch(url + toQueryString(queryParams), fetchOptions)
  } catch (err) {
    log.info(`Failed to make request, caused by ${err.message}`)
    return {
      body: false,
      error: 'FETCH',
      result: {
        ok: false,
        code: err.name,
        message: err.message
      }
    }
  }
  try {
    contentType = await response.headers.get('content-type')
  } catch (err) {
    log.debug(`Failed to get content-type:${err.message}.`)
  }

  try {
    let result = {
      ok: response.ok,
      code: response.status,
      message: response.statusText
    }
    let responseBody
    let error = !response.ok

    if (error) {
      log.info(`Received error response for request (status ${response.status}).`)
      /*
       * Handle a special-case error where the response body is a HTML page...
       * Throw away the body and so it is simply reported as 'Forbidden'.
       * TODO: Handle responses based on their type rather than checking for
       *    individual special cases...
       */
      if (response.status === 403 && contentType.includes('html')) {
        return {
          body: false,
          error: 'REQUEST',
          result
        }
      }

      /*
       * If the response indicates an error and has a body, resolve the body as JSON
       * but no body return an empty object then return a `REQUEST` error
       */
      const isJson = contentType && contentType.includes(contentTypes.jsonType)
      responseBody = isJson ? await response.json() : {}
      return {
        body: responseBody,
        error: 'REQUEST',
        result
      }
    } else if (response.status === 204) {
      /*
       * A `204 (No Content)` response indicates a success, but with no content to return.
       * Avoid parsing the response because there isn't one.
       */
      responseBody = {}

      log.info(`Finished request with successful response (status ${response.status}).`)
      return {
        body: responseBody,
        error: false,
        result
      }
    } else {
      /**
       * The SDK should only be parsing the responses as is expected without checking the content type of the response.
       * This is deterministic depending on the `responseType` passed in to the request through the request options.
       * However, if parsing with the expected responseType does fail, attempt parsing using the response's content-type
       * header before returning a parse error.
       */
      responseBody = {}
      try {
        switch (responseType) {
          case responseTypes.json:
            responseBody = await response.json()
            break
          case responseTypes.blob:
            responseBody = await response.blob()
            break
          case responseTypes.text:
            responseBody = await response.text()
            break
          case responseTypes.none:
            // Do not parse the response
            break
          default:
            // Should never reach here
            log.warn('Unexepected response type: ', responseType)
        }
      } catch (e) {
        log.warn(`Failed to parse with response type: ${responseType}. Error: ${e}`)

        // "Fallback" to try response parsing using the Content-Type header of the response
        log.debug('Attempting to parse with the response content-type')
        if (contentType) {
          // Need to clone the response as the response body can only be read/attempted to be read once.
          const responseClone = response.clone()
          if (contentType.includes(contentTypes.jsonType) || contentType.includes(contentTypes.vdnJsonType)) {
            responseBody = await responseClone.json()
          } else if (
            contentType.includes(contentTypes.plainTextType) ||
            contentType.includes(contentTypes.xmlTextType)
          ) {
            responseBody = await responseClone.text()
          } else if (contentType.includes(contentTypes.octetStream)) {
            responseBody = await responseClone.blob()
          } else {
            log.warn('Unexepected content-type of response: ', contentType)
          }
        } else {
          log.debug('No content-type set in response, returning an empty object as the body')
        }
      }

      log.info(`Finished request with successful response (status ${response.status}).`)
      return {
        body: responseBody,
        error: false,
        result
      }
    }
  } catch (err) {
    log.info(`Failed to parse response, caused by ${err.message}`)
    return {
      body: false,
      error: 'REQUEST',
      result: {
        ok: false,
        code: err.name,
        message: err.message
      }
    }
  }
}

// begin-test-code
export const __testonly__ = { makeRequest, watchRequests, handleRequest }
// end-test-code
