/**
 * The media devices available for use have changed.
 *
 * Information about the available media devices can be retrieved using the
 *    {@link media.getDevices} API.
 *
 * @public
 * @memberof media
 * @event devices:change
 * @example
 * // Listen for changes to available media devices.
 * client.on('devices:change', function () {
 *    // Retrieve the latest media device lists.
 *    const devices = client.media.getDevices()
 * })
 */
export const DEVICES_CHANGED = 'devices:change'

/**
 * The specified Tracks have been muted.
 *
 * A Track can be muted using the {@link media.muteTracks} API.
 * @public
 * @memberof media
 * @event media:muted
 * @param {Object} params
 * @param {Array<string>} params.tracks The list of Tracks that were muted.
 */
export const TRACKS_MUTED = 'media:muted'

/**
 * The specified Tracks have been unmuted.
 *
 * A Track can be unmuted using the {@link media.unmuteTracks}
 *    API.
 * @public
 * @memberof media
 * @event media:unmuted
 * @param {Object} params
 * @param {Array<string>} params.tracks The list of Tracks that were unmuted.
 */
export const TRACKS_UNMUTED = 'media:unmuted'

/**
 * The (media) source, associated with specified tracks, has been muted.
 * This event is generated outside the control of the SDK when the media source,
 * such as a microphone or camera, has been muted in a browser. As a result, all tracks
 * associated with that media source will generate such event.
 * @public
 * @memberof media
 * @event media:sourceMuted
 * @param {Object} params
 * @param {Array<string>} params.trackIds The track Ids that are affected as a result of media source being muted.
 */
export const TRACK_SOURCE_MUTED = 'media:sourceMuted'

/**
 * The (media) source, associated with specified tracks, has been unmuted.
 * This event is generated outside the control of the SDK when the media source,
 * such as a microphone or camera, has been unmuted in a browser. As a result, all tracks
 * associated with that media source will generate such event.
 * @public
 * @memberof media
 * @event media:sourceUnmuted
 * @param {Object} params
 * @param {Array<string>} params.trackIds The track Ids that are affected as a result of media source being unmuted.
 */
export const TRACK_SOURCE_UNMUTED = 'media:sourceUnmuted'
